* {
  box-sizing: border-box;
  margin: 0 auto;
  font-family: Arial, Helvetica, sans-serif;
}
body {
  background-color: rgb(103, 3, 94);
}
header {
  display: flex;
  flex-direction: column;
  margin: 40px auto;
  width: 50%;
  background-color: black;
  border-radius: 20px 20px 260px 260px;
  border: 6px double darkOrange;
  color: white;
  height: 310px;
}
h1 {
  width: 60%;
  align-self: center;
}
h3 {
  margin: 40px 60%;
  background-color: rgb(245, 118, 7);
  background-image: linear-gradient(
    to top left,
    rgba(0, 0, 0, 0.2),
    rgba(0, 0, 0, 0.2) 30%,
    rgba(243, 139, 139, 1)
  );
  padding: 35px 25px;
  width: 100px;
  height: 100px;
  color: white;
  border-radius: 50%;
  border: 3px double white;
}
nav ul {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  height: 100px;
  width: 1000px;
  background-color: black;
  border-radius: 30px;
  border: 6px double darkOrange;
  padding: 50px;
}
nav ul li {
  display: block;
  margin: 0 1vmin;
  width: 150px;
  height: 70px;
  line-height: 2.5;
  padding: 10px 20px;
  font-size: 1rem;
  text-align: center;
  color: #fff;
  text-shadow: 1px 1px 1px #000;
  border-radius: 20px;
  border: 6px double black;
  background-color: rgb(245, 118, 7);
  background-image: linear-gradient(
    to top left,
    rgba(0, 0, 0, 0.2),
    rgba(0, 0, 0, 0.2) 30%,
    rgba(243, 139, 139, 0.7)
  );
  box-shadow: inset 4px 4px 4px rgba(255, 255, 255, 0.6),
    inset -4px -4px 3px rgba(0, 0, 0, 0.6);
  outline: none;
}
nav ul li a {
  text-decoration: none;
}
nav ul li:hover {
  background-color: white;
  border: 6px double black;
}
