* {
  box-sizing: border-box;
  margin: 0 auto;
}
body {
  background-color: rgb(103, 3, 94);
  height: 7000px;
}
.desc1,
.desc3 {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  margin: 30px auto;
  background-color: rgb(243, 195, 122);
  width: 90vw;
  height: 600px;
  padding: 50px;
  line-height:1.5rem ;
}
.desc1a{
  padding:30px;
}
.awp{
  position: relative;
  top:50px;
  padding:30px;
}
.desc3 {
  height: 230px;
}

.desc2,
.desc2a,
.desc3 {
  margin: 30px auto;
  background-color: rgb(243, 195, 122);
  width: 80vw;
  height: 1350px;
}
.desc2a {
  height: 1050px;
}
.desc2 h2,
.desc2a h2 {
  padding: 20px;
  text-align: center;
}
.desc2 p,
.desc2a p {
  width: 1100px;
}
.desc3 {
  height: 500px;
}
.img1,
.img2,
.img3,
.img4 {
  padding: 50px 50px;
}
.img3 {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}
.mmu {
  position: relative;
  top:50px;
  padding:30px;
}
.m2mu {
  position: relative;
  top: 75px;
}
.m3mu {
  position: relative;
  top: 70px;
}
hr.new1 {
  border: 5px double rgb(103, 3, 94);
  border-radius: 30px;
  width: 300px;
  margin: 20px 0px;
}
hr.new2 {
  border: 5px double rgb(103, 3, 94);
  border-radius: 30px;
  width: 250px;
}
.jbtt{
  margin: 10px 50px;
  width: 100px;
  height: 70px;
  line-height: 2.5;
  padding: 10px 20px;
  font-size: 1rem;
  text-align: center;
  color: #fff;
  text-shadow: 1px 1px 1px #000;
  border-radius: 20px;
  border: 6px double black;
  background-color: rgb(59, 245, 7);
  background-image: linear-gradient(
    to top left,
    rgba(0, 0, 0, 0.2),
    rgba(0, 0, 0, 0.2) 30%,
    rgba(44, 242, 222, 0.7)
  );
  box-shadow: inset 4px 4px 4px rgba(255, 255, 255, 0.6),
    inset -4px -4px 3px rgba(0, 0, 0, 0.6);
  outline: none;
}

.mockups {
  height: 90px;
  padding: 30px 140px;
}
p {
  width: 500px;
  padding: 20px;
  font-size: larger;
}
.newschool {
  margin: 20px 70px;
  display: flex;
  width: 110px;
  height: 50px;
  line-height: 1.5;
  padding: 1px 24px;
  font-size: 1.5rem;
  text-align: center;
  color: white;
  text-shadow: 1px 1px 1px #000;
  border-radius: 20px;
  border: 6px double black;
  background-color: rgb(245, 170, 7);
  background-image: linear-gradient(
    to top left,
    rgba(0, 0, 0, 0.2),
    rgba(0, 0, 0, 0.2) 30%,
    rgba(243, 139, 139, 0.7)
  );
  box-shadow: inset 4px 4px 4px rgba(255, 255, 255, 0.6),
    inset -4px -4px 3px rgba(0, 0, 0, 0.6);
  outline: none;
  text-decoration: none;
}
.map{
  margin:50px;
  width: 110px;
  border-radius:20px ;
  background-color: rgb(103, 3, 94);
  color: white;
  padding:10px;

}
